import $ from 'jquery';
// import "@/libs/ztree/js/jquery.ztree.core.js"
import "ztree/js/jquery.ztree.core.min.js";
import "ztree/js/jquery.ztree.excheck.min.js";
import "ztree/css/metroStyle/metroStyle.css";
import {
  deviceattrTree,
  search,
  searchsave,
  mappingsearch
} from '../../utils/service';
import {
  DxSelection,
  DxDataGrid,
  DxColumn,
  DxFilterRow,
  DxPager,
  DxFilterPanel,
  DxPaging,
  DxHeaderFilter,
  DxScrolling
} from "devextreme-vue/data-grid";
import zhMessages from "devextreme/localization/messages/zh.json";
import {
  locale,
  loadMessages
} from "devextreme/localization";
export default {
  components: {
    DxSelection,
    DxDataGrid,
    DxColumn,
    DxFilterRow,
    DxPager,
    DxFilterPanel,
    DxPaging,
    DxHeaderFilter,
    DxScrolling
  },
  props: {},
  created() {
    loadMessages(zhMessages);
    locale(navigator.language);
  },
  data() {
    return {
      setting: {
        treeId: 'key',
        view: {
          showIcon: true, //是否显示 文件图标
          fontCss: {}, // ztree的字体颜色
        },
        data: {
          key: {
            name: 'title'
          },
        },
        callback: {
          onClick: (event, treeId, treeNode) => {
            this.zTreeOnClick(event, treeId, treeNode); //点击生成树的点击事件
          },
          // onClick: this.zTreeOnClick
        }
      },
      zNodes: '',
      treenode: '',
      title: '',
      datalist: '',
      total: 0,
      pagesize: 20,
      updatalistinfo: false,
      nodeinfoForm: '',
      rules: {
        code: [{
          required: true,
          message: "请输入设备类型编码!"
        }],
        name: [{
          required: true,
          message: "请输入设备类型名称!"
        }]
      },
    };
  },
  mounted() {
    this.getdeviceattrTree()
    this.getSearchDatalist()

  },
  computed: {},
  methods: {
    // ztree点击事件
    zTreeOnClick(event, treeId, treeNode) {
      this.title = treeNode.title
      this.title = this.title + ' / '
      console.log("object", treeNode.id);

      var formData = {
        typecode: treeNode.id
      }
      mappingsearch(formData, res => {
        this.total = res.count
        this.datalist = res.data
        if (this.datalist) {
          this.datalist.map((x, index) => {
            x.key = index + 1;
          });
        }
      });
    },
    // 获取ztree数据
    getdeviceattrTree() {
      var formData = {
        type: ''
      }
      deviceattrTree(formData, res => {
        this.treenode = res.data[0]
        this.zNodes = this.treenode
        $.fn.zTree.init($("#treeDemo"), this.setting, this.zNodes).expandAll(true);
      });
    },
    // 初始化数据
    getSearchDatalist() {
      // var formData = {
      //   pageindex: 1,
      //   pagesize: this.pagesize
      // }
      // search(formData, res => {
      //   this.total = res.count
      //   this.datalist = res.data
      //   this.datalist.map((x, index) => {
      //     x.key = index + 1;
      //   });
      // });
    },
    // 页码数改变
    handleCurrentChange(e) {
      var formData = {
        pageindex: e,
        pagesize: this.pagesize
      }
      search(formData, res => {
        this.total = res.count
        this.datalist = res.data
        this.datalist.map((x, index) => {
          x.key = index + 1;
        });
      });
    },
    handleSizeChange(e) {
      // console.log('当前多少页e :>> ', e);
      this.pagesize = e
    },
    // 数据编辑时打开弹窗
    updatalist(rowInfo) {
      this.nodeinfoForm = rowInfo.data
      // console.log('this.nodeinfoForm :>> ', this.nodeinfoForm);
      this.updatalistinfo = true
    },
    // 编辑、新增后保存信息
    handleSubmit() {
      this.updatalistinfo = false
      this.$refs.nodeinfoForm.validate(valid => {
        if (valid) {
          let formData = new FormData();
          for (let i in this.nodeinfoForm) {
            if (this.nodeinfoForm[i]) {
              formData.append(i, this.nodeinfoForm[i]);
            }
          }
          formData.append('id', this.nodeinfoForm.id);
          searchsave(formData, res => {
            this.total = res.count
            this.datalist = res.data
            this.datalist.map((x, index) => {
              x.key = index + 1;
            });
          });
          this.getSearchDatalist()
        }
      });

    },
    handleToggleDialog() {
      this.updatalistinfo = false;
    },
  },
};



